import React , { useState } from 'react'
import { Button, Input } from '../../../styles'
import { LayoutContainer } from '../../shared'
import { useNavigate } from 'react-router-dom'
import GoVerified from '@meronex/icons/go/GoVerified'
import AiFillEye from '@meronex/icons/ai/AiFillEye'
import { useForm } from 'react-hook-form'
import { useApi } from '../../../contexts/ApiContext'
import { useSession } from '../../../contexts/SessionContext'
import { toast } from 'react-toastify'
import axios from 'axios'
import Switch from '@mui/material/Switch';
import { styled } from '@mui/material/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import PhoneInput from 'react-phone-input-2'


import {
  Container,
  InnerContainer,
  Head,
  Heading,
  HeadDes,
  FormWraper,
  FormRow,
  FormLabel,
} from './styles'

const MaterialUISwitch = styled(Switch)(({ theme }) => ({
  width: 102,
  height: 54,
  padding: 7,
  '& .MuiSwitch-switchBase': {
    margin: 1,
    padding: 0,
    transform: 'translateX(0px)',
    '&.Mui-checked': {
      transform: 'translateX(50px)',
      '& .MuiSwitch-thumb:before': {
        backgroundImage: `url('./images/email.png')`,
      },
      '& + .MuiSwitch-track': {
        opacity: 1,
        backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
      },
    },
  },
  '& .MuiSwitch-thumb': {
    backgroundColor: theme.palette.mode === 'dark' ? '#003892' : '#001e3c',
    width: 50,
    height: 50,
    '&:before': {
      content: "''",
      position: 'absolute',
      width: '100%',
      height: '100%',
      left: 0,
      top: 0,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundImage: `url('./images/phone.png')`
    },
  },
  '& .MuiSwitch-track': {
    opacity: 1,
    backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
    borderRadius: 20 / 2,
  },
}));

export const ForgotPassword = () => {
  const nagative = useNavigate()
  const { register, handleSubmit, formState: { errors }} = useForm()
  const [checked, setChecked] = React.useState(true);
  const [value, setValue] = useState()

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setChecked(event.target.checked);
  };
  const onSubmit = async (values) => {
    await axios.post("/api/users/forgetpassword", {
      useremail: values.email,
      checked: checked,
      phonenumber: value,
    })
    .then(res => {
      toast('Password reseted correctly.', { type: 'success'})
      nagative('/login')
    })
    .catch(err => {
      console.log(err)
      toast(err.response.data, { type: 'error'})
    });
  }

  return (
    <LayoutContainer>
      <Container>
        <InnerContainer>
          <Head>
            <Heading>
              Forgot Password
              <br></br>
              <FormControlLabel
                control={<MaterialUISwitch sx={{ m: 1 }} defaultChecked />}
                label="Reset Password With"
                checked={checked}
                onChange={handleChange}
                labelPlacement="start"
              />
            </Heading>
            
            {checked && <HeadDes>To recover your BlockReward account, enter your email address below</HeadDes>}
            {!checked && <HeadDes>To recover your BlockReward account, enter your phonenumber below</HeadDes>}
          </Head>
          <FormWraper onSubmit={handleSubmit(onSubmit)}>
          { checked && <FormRow error={errors?.email}>
              <FormLabel>EMAIL</FormLabel>
              <Input
                placeholder='Enter email address'
                autoComplete='off'
                {...register(
                  'email',
                  {
                    required: {
                      value: true,
                      message: 'The field is required*'
                    }
                  }
                )}
              />
            </FormRow>}
            { !checked && <FormRow error={errors?.phonenumber}>
              <FormLabel>PHONENUMBER</FormLabel>
              <PhoneInput
                country={'us'}
                specialLabel=''
                enableTerritories
                value={value}
                onChange={setValue}
              />
            </FormRow>}
            <FormRow>
              <Button color="primary" type='submit'>Continue</Button>
            </FormRow>
            <FormRow>
              BlockReward <a href="./terms" target="_blank" rel="noreferrer">Terms and Conditions</a> and <a href="./privacy" target="_blank" rel="noreferrer">Privacy Policy</a> apply. This site is protected by Google reCAPTCHA.
            </FormRow>
          </FormWraper>
        </InnerContainer>
      </Container>
    </LayoutContainer>
  )
}