import React, { useState } from 'react'
import { LayoutContainer } from '../../shared'
import { useNavigate } from 'react-router-dom'
import GoVerified from '@meronex/icons/go/GoVerified'
import { Button, Input } from '../../../styles'
import AiFillEye from '@meronex/icons/ai/AiFillEye'
import { useForm } from 'react-hook-form'
import { useSession } from '../../../contexts/SessionContext'
import { useApi } from '../../../contexts/ApiContext'
import { toast } from 'react-toastify'
import axios from 'axios'
import PhoneInput from 'react-phone-input-2'
import Switch from '@mui/material/Switch';
import { styled } from '@mui/material/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import './material.css'

import {
  Container,
  LeftWrapper,
  RightWrapper,
  Details,
  DetailContent,
  Head,
  Heading,
  HeadDes,
  FormWraper,
  FormRow,
  FormLabel,
  InputWrapper,
  ErrorMessage
} from './styles'

const MaterialUISwitch = styled(Switch)(({ theme }) => ({
  width: 102,
  height: 54,
  padding: 7,
  '& .MuiSwitch-switchBase': {
    margin: 1,
    padding: 0,
    transform: 'translateX(0px)',
    '&.Mui-checked': {
      transform: 'translateX(50px)',
      '& .MuiSwitch-thumb:before': {
        backgroundImage: `url('./images/email.png')`,
      },
      '& + .MuiSwitch-track': {
        opacity: 1,
        backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
      },
    },
  },
  '& .MuiSwitch-thumb': {
    backgroundColor: theme.palette.mode === 'dark' ? '#003892' : '#001e3c',
    width: 50,
    height: 50,
    '&:before': {
      content: "''",
      position: 'absolute',
      width: '100%',
      height: '100%',
      left: 0,
      top: 0,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundImage: `url('./images/phone.png')`
    },
  },
  '& .MuiSwitch-track': {
    opacity: 1,
    backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
    borderRadius: 20 / 2,
  },
}));


export const SignUp = () => {
  const nagative = useNavigate()
  const [{ auth }, { login } ] = useSession()
  const [{ doPost }] = useApi()
  const { register, handleSubmit, formState: { errors }} = useForm()
  const [isLoading, setIsLoading] = useState(false)
  const [isShowPassword, setIsShowPassword] = useState(false);
  const [value, setValue] = useState()
  const [checked, setChecked] = React.useState(true);
  
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setChecked(event.target.checked);
  };
  const onSubmit = async (values) => {
    /*try {
      setIsLoading(true)
      const response = await doPost('api/auth/signup', values)
      if (response.error) {
        throw response.result
      }
      setIsLoading(false)
      toast('Success', { type: 'success' })
      await login(response.result)

      nagative('/u/dashboard')
    } catch (error) {
      toast(error, { type: 'error' })
      setIsLoading(false)
    }
    */
    setIsLoading(true)
    console.log(value)
    await axios.post("/api/users/signup", {
      useremail: values.email,
      password: values.password,
      phonenumber: value,
      checked: checked,
    })
    .then(res => {
      console.log(res)
      setIsLoading(false)
      localStorage.setItem("useremail", values.email);
      nagative('/verify-account')
    })
    .catch(err => {
      toast(err.response.data, { type: 'error'})
      setIsLoading(false)
    });
  }

  return (
    <LayoutContainer>
      <Container>
        <LeftWrapper onClick={() => nagative('/landing/aitch/')}>
          <img src="https://limewire.com/hc_content_aitch_cover.92bbc219.jpg" alt='' />
          <Details>
            <div>
              <div>Close To Home</div>
              <DetailContent>
                <img src='https://d2tg7hjmtin7hh.cloudfront.net/thumbs/creator_page_avatar/2843/28433c6b-8efa-453f-bd4f-73b4314862fc_small.png' alt='' />
                <span>Aitch</span>
                <GoVerified />
              </DetailContent>
            </div>
            <div>
              Cooming Soon
            </div>
          </Details>
        </LeftWrapper>

        <RightWrapper>
          <Head>
            <Heading>Sign Up<br></br>
            <FormControlLabel
              control={<MaterialUISwitch sx={{ m: 1 }} defaultChecked />}
              label="WITH"
              checked={checked}
              onChange={handleChange}
              labelPlacement="start"
            />
            </Heading>
            <HeadDes>Join BlockReward to buy, sell and browse NFTs</HeadDes>
            
          </Head>
          <FormWraper onSubmit={handleSubmit(onSubmit)}>
            { checked && <FormRow error={errors?.email}>
              <FormLabel>EMAIL</FormLabel>
              <Input
                placeholder='Enter email address'
                autoComplete='off'
                {...register(
                  'email',
                  {
                    required: {
                      value: true,
                      message: 'The field is required*'
                    }
                  }
                )}
              />
              {errors?.email && <ErrorMessage>{errors?.email?.message}</ErrorMessage>}
            </FormRow>}
            { !checked && <FormRow error={errors?.phonenumber}>
              <FormLabel>PHONENUMBER</FormLabel>
              <PhoneInput
                country={'us'}
                specialLabel=''
                enableTerritories
                value={value}
                onChange={setValue}
              />
              {/* <Input
                placeholder='Enter phonenumber'
                autoComplete='off'
                {...register(
                  'phonenumber',
                  {
                    required: {
                      value: true,
                      message: 'The field is required*'
                    }
                  }
                )}
              /> */}
              {errors?.phonenumber && <ErrorMessage>{errors?.phonenumber?.message}</ErrorMessage>}
            </FormRow>}
            <FormRow error={errors?.password}>
              <FormLabel>
                <span>Password</span>
              </FormLabel>
              <InputWrapper>
                <Input
                  type={isShowPassword ? 'text' : 'password'}
                  autoComplete='off'
                  {...register(
                    'password',
                    {
                      required: {
                        value: true,
                        message: 'The field is required*'
                      },
                      maxLength: {
                        value: 30,
                        message: `The characters must be less than 30`
                      },
                      minLength: {
                        value: 8,
                        message: `The characters must be more than 8`
                      }
                    }
                  )}
                />
                <AiFillEye onClick={()=>{setIsShowPassword(!isShowPassword)}}/>
              </InputWrapper>
              {errors?.password && <ErrorMessage>{errors?.password?.message}</ErrorMessage>}
            </FormRow>
            <FormRow>
              By signing up, you agree to the <a href="./terms" target="_blank" rel="noreferrer">Terms and Conditions</a> and <a href="./privacy" target="_blank" rel="noreferrer">Privacy Policy</a> and to receive updates from BlockReward.
            </FormRow>
            <FormRow>
              <Button
                color="primary"
                type='submit'
                isLoading={isLoading}
              >
                Sign Up
              </Button>
            </FormRow>
          </FormWraper>
        </RightWrapper>
      </Container>
    </LayoutContainer>
  )
}